<template>
  <div class="gamer-bio critical-component-clear">
    <div class="container gamer-bio__container">
      <div class="row gamer-bio__heading">
        <div
          v-if="$slots.image"
          class="col xs12 m6"
        >
          <slot name="image" />
        </div>

        <div class="col xs12 m6">
          <div
            v-if="settings.gamer_name"
            ref="bioName"
            class="h3 gamer-bio__name"
            v-text="settings.gamer_name"
          />

          <div
            v-if="hasSocials"
            class="gamer-bio__socials"
          >
            <a
              v-for="(url, socialName) in socials"
              :key="socialName"
              :href="url"
              aria-label="social-icon"
              class="gamer-bio__icon"
            >
              <component :is="socialName" />
            </a>
          </div>

          <div
            class="gamer-bio__fields"
          >
            <div
              v-for="index in 4"
              :key="`field-${index}`"
              class="gamer-bio__field"
            >
              <b
                v-if="settings[`field_${index}_title`] !== ''"
                v-text="settings[`field_${index}_title`]"
              />
              {{ settings[`field_${index}_desc`] }}
            </div>
          </div>
        </div>
      </div>

      <!-- Mobile description -->
      <template
        v-if="settings.first_column_text && settings.second_column_text"
      >
        <div
          ref="mobileDescription"
          :class="{[$class('collapsed')]: collapsed}"
          class="gamer-bio__description h6 mobile"
        >
          <template
            v-if="settings.truncate &&
              fullDescription.length >= settings.truncate_min"
          >
            <div
              class="gamer-bio__short"
              v-html="truncatedDescription"
            />

            <div
              class="gamer-bio__full"
              v-html="fullDescription"
            />

            <button
              class="gamer-bio__expand"
              @click="toggleCollapse"
              v-text="expandLabel"
            />
          </template>
          <div
            v-else
            class="gamer-bio__full"
            v-html="fullDescription"
          />
        </div>
      </template>

      <!-- Desktop description -->
      <div
        :class="{'gamer-bio__description--one': isOneColumn}"
        class="row gamer-bio__description desktop"
      >
        <template
          v-for="value in ['first_column_text', 'second_column_text']"
          :key="value"
        >
          <div
            v-if="settings[value]"
            :class="columnClass"
            class="col xs12 body-1"
            v-html="settings[value]"
          />
        </template>
      </div>

      <!-- Download Links -->
      <div
        v-if="settings.download_header"
        class="row col xs12 m6 h5 gamer-bio__wallpaper"
        v-html="settings.download_header"
      />

      <div class="row">
        <template
          v-for="download in downloads"
          :key="download.header"
        >
          <div
            v-if="download.header || download.links.length"
            class="col xs6 m3"
          >
            <div
              v-if="download.header"
              class="label gamer-bio__label"
              v-html="download.header"
            />

            <div
              v-if="download.links.length"
              class="gamer-bio__links"
            >
              <a
                v-for="link in download.links"
                :key="link.title"
                :href="link.href"
                :target="link.target"
                :title="link.title"
                class="text-link"
              >
                {{ link.textContent }}
                <icon-arrow-up-right />
              </a>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>

/**
 * Vue: Gamer bio (gamer-bio)
 * -----------------------------------------------------------------------------
 * Display HyperX social links and the influencer's photo and mini biography.
 *
 * @param {String} liquid.sectionId - Section's Liquid ID.
 * @param {Object} settings - The section settings.
 *
 */

import { defineAsyncComponent } from 'vue'
import ResponsiveImage from '~global/images/responsive-image'
import IconArrowUpRight from '~icons/direction/arrow/up-right.svg'

function loadIcons() {
  const icons = {}
  Object.keys(cnvs.socials.urls).forEach(social => {
    icons[social.toLowerCase()] = defineAsyncComponent(() =>
      import(`~icons/social/${social.toLowerCase()}.svg`),
    )
  })
  return icons
}

export default {
  name: 'GamerBio',
  components: {
    IconArrowUpRight,
    ResponsiveImage,
    ...loadIcons(),
  },
  props: {
    liquid: {
      type: Object,
      default: () => ({
        sectionId: '',
      }),
    },
    settings: {
      type: Object,
      default: () => ({}),
    },
  },

  data() {
    return {
      collapsed: true,
      downloads: [
        {
          header: this.settings.download_desktop_header,
          links: this.getLinks('desktop'),
        },
        {
          header: this.settings.download_mobile_header,
          links: this.getLinks('mobile'),
        },
      ],
      socials: this.$variable('socials.urls'),
    }
  },

  computed: {

    /**
     * Compute column class
     * @returns {string}
     */
    columnClass() {
      return this.settings.first_column_text && this.settings.second_column_text
        ? 'm6'
        : 'm12'
    },

    /**
     * Compute the expand button label.
     * @returns {string}
     */
    expandLabel() {
      return this.collapsed
        ? this.$string('general.gamer_bio.read_more')
        : this.$string('general.gamer_bio.read_less')
    },

    /**
     * Compute full description HTML
     * @returns {string}
     */
    fullDescription() {
      return `${this.settings.first_column_text}

      ${this.settings.second_column_text}
      `.trim()
    },

    /**
     * Compute if there are social icons
     * @returns {boolean}
     */
    hasSocials() {
      return Object.keys(this.socials).length > 0
    },

    /**
     * Compute if description should be a single column.
     * @returns {boolean}
     */
    isOneColumn() {
      return !this.settings.first_column_text || !this.settings.second_column_text
    },

    /**
     * Compute the truncated description HTML.
     * @returns {string}
     */
    truncatedDescription() {
      return this.fullDescription.slice(0, this.settings.truncate_at) + ' ... </p>'
    },
  },

  methods: {

    /**
     * Get wallpaper download links
     * @param {String} device - The name of the device, mobile or desktop.
     * @returns {{href: String, title: String, target: String}[]}
     */
    getLinks(device) {
      const div = document.createElement('div')
      div.innerHTML = this.settings[`download_${device}`]
      const links = [...div.querySelectorAll('a')]
      return links.map(link => {
        return {
          href: link.href,
          target: link.target,
          title: link.title,
          textContent: link.textContent,
        }
      })
    },

    /**
     * Toggle read more / read less.
     */
    toggleCollapse() {
      const { mobileDescription, bioName } = this.$refs
      document.documentElement.style.scrollBehavior = 'auto'

      if (!mobileDescription) {
        return
      }

      this.collapsed = !this.collapsed

      const top = mobileDescription.getBoundingClientRect().top
      if (!this.collapsed) {
        // Retain visible position from where user expanded
        mobileDescription.scrollIntoView()
        window.scrollTo(0, window.scrollY - top)
      } else if (top < 0 && bioName) {
        // Scroll to top of component if user scrolled below
        bioName.scrollIntoView()
      }

      document.documentElement.style.scrollBehavior = ''
    },
  },
}
</script>

<style lang="scss">
@import './gamer-bio';
</style>
